import React from 'react';
import { Card, Row, Col, Badge } from 'react-bootstrap';
import { FaUsers, FaShoppingCart, FaDollarSign, FaBox, FaStar, FaCalendarAlt } from 'react-icons/fa';
import { Line } from 'react-chartjs-2';
import Layout from '@/components/Layout';
import styles from '../styles/Dashboard.module.css';
import './Dashboard.css'; // Optional CSS for custom styling

const Dashboard = () => {
  const salesChartData = {
    labels: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul'],
    datasets: [
      {
        label: 'Sales ($)',
        data: [500, 700, 800, 900, 1200, 1500, 1700],
        borderColor: '#36A2EB',
        backgroundColor: 'rgba(54, 162, 235, 0.2)',
        tension: 0.4,
        pointRadius: 5,
        pointBackgroundColor: '#36A2EB',
      },
    ],
  };

  const chartOptions = {
    responsive: true,
    plugins: {
      legend: { display: true, position: 'top' },
    },
    scales: {
      x: { ticks: { color: '#555' }, grid: { display: false } },
      y: {
        ticks: { callback: (value) => `$${value}`, color: '#555' },
        grid: { color: '#e0e0e0' },
      },
    },
  };

  const recentOrders = [
    { id: 1, customer: 'John Doe', date: '2024-01-25', amount: '$120' },
    { id: 2, customer: 'Jane Smith', date: '2024-01-24', amount: '$80' },
  ];

  const topSellingProducts = [
    { id: 1, product: 'Product A', sales: '$1,200' },
    { id: 2, product: 'Product B', sales: '$1,000' },
  ];

  const recentCustomers = [
    { id: 1, name: 'John Doe', joined: '2024-01-25', status: 'Active' },
    { id: 2, name: 'Jane Smith', joined: '2024-01-24', status: 'New' },
  ];

  return (
    <Layout>
      <div className={styles.dashboard}>
        {/* Stats Section */}
        <Row className="g-4">
          <Col xs={12} sm={6} md={3}>
            <Card className={`${styles.statCard} gradient-card`}>
              <Card.Body className="d-flex align-items-center">
                <FaUsers className="icon-large text-light" />
                <div className="ms-3 text-light">
                  <h5>Users</h5>
                  <p>1,234</p>
                </div>
              </Card.Body>
            </Card>
          </Col>
          <Col xs={12} sm={6} md={3}>
            <Card className={`${styles.statCard} gradient-card`}>
              <Card.Body className="d-flex align-items-center">
                <FaShoppingCart className="icon-large text-light" />
                <div className="ms-3 text-light">
                  <h5>Orders</h5>
                  <p>567</p>
                </div>
              </Card.Body>
            </Card>
          </Col>
          <Col xs={12} sm={6} md={3}>
            <Card className={`${styles.statCard} gradient-card`}>
              <Card.Body className="d-flex align-items-center">
                <FaDollarSign className="icon-large text-light" />
                <div className="ms-3 text-light">
                  <h5>Revenue</h5>
                  <p>$12,345</p>
                </div>
              </Card.Body>
            </Card>
          </Col>
          <Col xs={12} sm={6} md={3}>
            <Card className={`${styles.statCard} gradient-card`}>
              <Card.Body className="d-flex align-items-center">
                <FaBox className="icon-large text-light" />
                <div className="ms-3 text-light">
                  <h5>Products</h5>
                  <p>456</p>
                </div>
              </Card.Body>
            </Card>
          </Col>
        </Row>

        {/* Main Content Section */}
        <Row className="mt-4">
          {/* Sales Report */}
          <Col xs={12} lg={8}>
            <Card className="shadow-sm">
              <Card.Body>
                <h5>Sales Report</h5>
                <Line data={salesChartData} options={chartOptions} />
              </Card.Body>
            </Card>
          </Col>

          {/* Recent Orders */}
          <Col xs={12} lg={4}>
            <Card className="shadow-sm">
              <Card.Body>
                <h5>Recent Orders</h5>
                <div className="order-list">
                  {recentOrders.map((order) => (
                    <div key={order.id} className="order-item p-3">
                      <div className="d-flex justify-content-between">
                        <strong>{order.customer}</strong>
                        <span>{order.amount}</span>
                      </div>
                      <small className="text-muted">{order.date}</small>
                    </div>
                  ))}
                </div>
              </Card.Body>
            </Card>
          </Col>
        </Row>

        {/* Top Selling Products and Recent Customers */}
        <Row className="mt-4">
          <Col xs={12} lg={8}>
            <Card className="shadow-sm">
              <Card.Body>
                <h5>Top Selling Products</h5>
                <div className="product-list">
                  {topSellingProducts.map((product) => (
                    <div key={product.id} className="product-item p-3 d-flex align-items-center">
                      <FaStar className="text-warning me-2" />
                      <div>
                        <strong>{product.product}</strong>
                        <span className="d-block text-muted">{product.sales}</span>
                      </div>
                    </div>
                  ))}
                </div>
              </Card.Body>
            </Card>
          </Col>

          <Col xs={12} lg={4}>
            <Card className="shadow-sm">
              <Card.Body>
                <h5>Recent Customers</h5>
                <div className="customer-list">
                  {recentCustomers.map((customer) => (
                    <div
                      key={customer.id}
                      className="customer-item p-3 d-flex align-items-center justify-content-between"
                    >
                      <div>
                        <strong>{customer.name}</strong>
                        <small className="d-block text-muted">{customer.joined}</small>
                      </div>
                      <Badge bg={customer.status === 'New' ? 'success' : 'secondary'}>
                        {customer.status}
                      </Badge>
                    </div>
                  ))}
                </div>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </div>
    </Layout>
  );
};

export default Dashboard;
